<template>
    <div class="recruitDetail">
        <div class="contant">
            <!--  -->
            <div class="information-header">
                <div class="user">
                    <span class="user-bg"></span>
                    <p class="strong">xxx</p>
                    <p><span></span> <span></span></p>
                </div>
                <!--<p class="money">$ 1000</p>-->
            </div>
            <!--  -->
            <div class="cont-text">
                <p></p>
                <img src="../../assets/img/banner-1.png" alt="">
                <p></p>
            </div>
        </div>
        <div class="contact-box">
        	<h3></h3>
            <p>
            	<img src="../../assets/img/icon-life-add-night@2x.png"/>
            	XXX
            </p>
            <h3></h3>
            <p> 13526626357</p>
            <p>facebook: 111111</p>
        </div>
		<!---->
        <div class="dis-recommend">
        	<p class="title"></p>
            <recruitmend-list></recruitmend-list>
        </div>
        <share-box></share-box>
    </div>
</template>
<script>

import shareBox from "../../components/common/share-box.vue";
import recruitmendList from "./children/recruitmend-list.vue";
export default {
    data() {
        return {};
    },
    components: {
        shareBox,
        recruitmendList
    },
    activated() {
    	console.log(document.querySelector(".recruitDetail").scrollTop);
    }
}
</script>
<style scoped lang="scss">
.recruitDetail{
	height: 100%;
    overflow: auto;
    -webkit-overflow-scrolling: touch;
    position: relative;
}
.contant {
    padding: 30px;
}
/**/ 
.information-header {
    padding: 30px 0 20px 0;
    font-size: 28px;
    font-weight: bold;
}
.user-bg {
    float: left;
    width: 72px;
    height: 72px;
    background: url("../../assets/img/icon-User-Avatar.png") no-repeat left top;
    background-size: cover;
    margin: 0 20px 0 0;
}
span{
    font-weight: normal;
    color: #999999;
    font-size: 24px;
}
// 
.money {
    font-size: 36px;
    color: #f05c28;
    margin: 30px 0 0 0;
    
}
// 
.cont-text{
    font-size: 36px;
    line-height: 1.5;
    img{
        width: 100%;
        margin: 18px 0;
    }
}
.contact-box{
    color: black;
    border-bottom: 10px solid #cecece;
    padding: 0 30px;
    h3{
        font-size: 36px;
        font-weight: bold;
        margin: 20px 0 30px 0;
    }
    p{
        font-size: 30px;
        margin: 0 0 26px 0;
        img{
        	display: block;
        	float: left;
        	width: 26px;
        	height: 34px;
        	margin-right: 18px;
        	vertical-align: middle;
        }
    }
    p:last-child{
    	margin: 0 0 38px 0;
    }
}
.dis-recommend{
	padding: 0 30px;
	.title{
		font-size: 30px;
		color: #747474;
		margin-top: 34px;
		margin-bottom: 32px;
	}
}
</style>



