<template>
	<div class="merchant" v-infinite-scroll="loadBottomAjax" infinite-scroll-disabled="bottomLock" infinite-scroll-distance="10" infinite-scroll-immediate-check="false" >
		<!--  -->
		<mt-loadmore :top-method="loadTopAjax" @top-status-change="handleTopChange" ref="loadmore">
			<!--  -->
			<div slot="top" class="mint-loadmore-top">
				<span v-show="topStatus == 'pull'">  </span>
				<span v-show="topStatus == 'drop'"> </span>
				<span v-show="topStatus == 'loading'"></span>
			</div>
			<!--  -->
			<!---->
			<banner></banner>
			<!---->
			<div class="serviceType">
				<ul>
					<li @click="toList(1,'')">
						<img src="../../assets/img/icon-ms@2x.png" />
						<span></span>
					</li>
					<li @click="toList(2,'')">
						<img src="../../assets/img/icon-yl@2x.png" />
						<span></span>
					</li>
					<li @click="toList(3,'')">
						<img src="../../assets/img/icon-lvy@2x.png" />
						<span></span>
					</li>
					<li @click="toList(4,'')">
						<img src="../../assets/img/icon-gw@2x.png" />
						<span></span>
					</li>
					<li @click="toList(5,'')">
						<img src="../../assets/img/icon-js@2x.png" />
						<span></span>
					</li>
					<li @click="toList(6,'')">
						<img src="../../assets/img/icon-yil@2x.png" />
						<span></span>
					</li>
					<li @click="toList(7,'')">
						<img src="../../assets/img/icon-jy@2x.png" />
						<span></span>
					</li>
					<li @click="toList(8,'')">
						<img src="../../assets/img/icon-fw@2x.png" />
						<span></span>
					</li>
					<div class="clear"></div>
				</ul>
			</div>

			<!---->
			<div class="recommend">
				<p class="rec_title"></p>
				<list-view></list-view>
			</div>
			<!--  -->
			<div class="bottomLoad" v-if="contentJson.length > 0">
				<div class="loading" v-show='bottomLoading'>...</div>
				<div class="noData" v-if='bottomNoData'></div>
			</div>
		</mt-loadmore>

	</div>
</template>

<script>
	import { mapState, mapGetters, mapMutations, mapActions } from 'vuex'
	import banner from "../../components/common/banner.vue";
	import listView from "./children/list-view.vue";
	import Swiper from "swiper";
	// mint-ui 
	import { Loadmore, InfiniteScroll } from 'mint-ui';

	export default {
		data() {
			return {
				receptionMSG: false, //

				topStatus: '', // 
				bottomLock: false, // 
				bottomLoading: true, // loading
				bottomNoData: false, // nothing
				contentJson: [1, 2], // 
			};
		},
		components: {
			banner,
			listView
		},
		mounted() {},
		computed: {
			...mapState('commom', [
				'popUp',
				'popUpType'
			])
		},
		watch: {},
		methods: {
			...mapMutations('commom', [
				'set_popUp'
			]),
			...mapMutations('merchant', [
				'setHeaderText'
			]),

			//
			more: function() {
				this.receptionMSG = true;
				this.set_popUp(true);
			},

			// mint-ui 
			handleTopChange(status) {
				this.topStatus = status;
			},

			// 
			loadTopAjax() {
				setTimeout(() => {
					this.$refs.loadmore.onTopLoaded();
				}, 1500);
			},
			// 
			loadBottomAjax() {
				this.bottomLock = true; // 
				setTimeout(() => {
					let arr1 = [3, 3, 4, 5, 6, 7, 8, 99, 9, 0, 2];
					this.contentJson.push(arr1);
					this.bottomLock = false; // 
					console.log('aaa');
				}, 1000);
				// alert(1);
				console.log(this.contentJson);
				this.bottomNoData = false;
				// this.contentJson = [];
			},
			//
			toList(id, content) {
				this.$router.push('/typesList/' + id);
				//
				this.setHeaderText(content);
			},
			// 
			handleLocaltion(type) {
				if(type === 'get') {
					this.$nextTick(() => {
						document.querySelector(".DisContainer").scrollTop = sessionStorage.getItem('disScrollTop');
					})
				} else if(type === 'set') {
					let scrollTop = document.querySelector(".DisContainer").scrollTop;
					sessionStorage.setItem('disScrollTop', scrollTop);
				}
			},
			saveScroll() {
				this.handleLocaltion('set')
			},
		},
		activated() {
			this.handleLocaltion('get');
			window.addEventListener('beforeunload', this.saveScroll); // 
		},
		deactivated() {
			this.handleLocaltion('set');
			window.addEventListener('beforeunload', this.saveScroll); // 
		}
	};
</script>

<style scoped lang="scss">
	.merchant {
		height: 100%;
		overflow: auto;
		-webkit-overflow-scrolling: touch;
		.serviceType {
			width: 100%;
			border-bottom: 8px solid #f2f2f2;
			padding: 48px 64px 8px;
			ul {
				li {
					display: block;
					float: left;
					text-align: center;
					margin-bottom: 24px;
					margin-right: 100px;
					img {
						width: 80px;
						height: 80px;
						margin-bottom: 16px;
					}
					span {
						font-size: 26px;
						color: #333;
					}
				}
				li:nth-child(4n) {
					margin-right: 0;
				}
			}
		}
		.recommend {
			padding: 30px;
			.rec_title {
				font-size: 32px;
				color: #333;
			}
		}
	}
	.bottomLoad {
        width: 100%;
        position: relative;
        div {
            width: 100%;
            height: 50px;
            line-height: 50px;
            text-align: center;
            font-size: 16px;
            color: #999;
        }
    }
	.clear {
		clear: both;
	}
</style>


