export default {
	namespaced: true,
	state: {
		nighttime: false, //
		popUp: false,//
		popUpType: 0, //0:,123

		detailPop: false //
	},
	mutations: {
		set_nighttime(state, val){
			state.nighttime = val;
		},
		set_popUp(state, val){
			state.popUp = val;
		},
		set_popUpType(state, val){
			state.popUpType = val;
		},
		//
		set_detailPop(state, val){
			state.detailPop = val;
		}
	}
}



