<template>
    <div class="DisContainer" v-infinite-scroll="loadBottomAjax" infinite-scroll-disabled="bottomLock" infinite-scroll-distance="10" infinite-scroll-immediate-check="false" >
        <!--  -->
        <mt-loadmore :top-method="loadTopAjax" @top-status-change="handleTopChange" ref="loadmore">
             <!--  -->
            <div slot="top" class="mint-loadmore-top">
                <span v-show="topStatus == 'pull'">  </span>
                <span v-show="topStatus == 'drop'"> </span>
                <span v-show="topStatus == 'loading'"></span>
            </div>
            <!--  -->
            <!---->
    		<banner></banner>
	        <ul class="live-shop-list">
	            <li>
	                <!--  -->
	                <div class="information-header clearfix">
	                    <div class="user fl-left">
	                        <span class="user-bg"></span>
	                        <p class="strong">xxx</p>
	                        <p><span></span> <span></span></p>
	                    </div>
	                    <!--<span class="money fl-right">$ 10000</span>-->
	                </div>
	                <router-link to="/discountsDetail/1">
	                    <h2 class="information-in"></h2>
	                    <div class="pic-box pic-cont01">
	                        <span><img src="../../assets/test/img/list-pc01.jpg" alt=""></span>
	                        <span><img src="../../assets/test/img/list-pc01.jpg" alt=""></span>
	                        <span><img src="../../assets/test/img/list-pc01.jpg" alt=""></span>
	                        <span><img src="../../assets/test/img/list-pc01.jpg" alt=""></span>
	                        <span><img src="../../assets/test/img/list-pc01.jpg" alt=""></span>
	                        <span><img src="../../assets/test/img/list-pc01.jpg" alt=""></span>
	                        <span><img src="../../assets/test/img/list-pc01.jpg" alt=""></span>
	                        <span><img src="../../assets/test/img/list-pc01.jpg" alt=""></span>
	                        <span><img src="../../assets/test/img/list-pc01.jpg" alt=""></span>
	                    </div>
	                </router-link>
	                <!-- -->
	                <div class="list-tail">
	                    <span class="classified"> </span>
	                    <span></span>
	                    <span></span>
	                    <span></span>
	                    <span class="more" @click="more"></span>
	                </div>
	            </li>
	            <li>
	                <!--  -->
	                <div class="information-header clearfix">
	                    <div class="user fl-left ">
	                    	<span class="user-bg"></span>
	                        <p class="mg-top strong"></p>
	                        <p><span></span> <span></span></p>
	                    </div>
	                    <!--<span class="money fl-right">$ 10000</span>-->
	                </div>
	                <router-link to="/discountsDetail/2">
	                    <h2 class="information-in"></h2>
	                    <div class="pic-box pic-cont02">
	                        <span><img src="../../assets/test/img/list-pc01.jpg" alt=""></span>
	                        <span><img src="../../assets/test/img/list-pc01.jpg" alt=""></span>
	                    </div>
	                </router-link>
	                <!-- -->
	                <div class="list-tail">
	                    <span class="classified"></span>
	                    <span></span>
	                    <span></span>
	                    <span></span>
	                    <span class="more" @click="more"></span>
	                </div>
	            </li>
	            <li>
	                <!--  -->
	                <div class="information-header clearfix">
	                    <div class="user fl-left">
	                        <span class="user-bg"></span>
	                        <p class="strong">xxx</p>
	                        <p><span></span> <span></span></p>
	                    </div>
	                    <!--<span class="money fl-right">$ 10000</span>-->
	                </div>
	                <router-link to="/discountsDetail/3">
	                    <h2 class="information-in"></h2>
	                    <div class="pic-box pic-cont03">
	                        <span><img src="../../assets/test/img/list-pc01.jpg" alt=""></span>
	                    </div>
	                </router-link>
	                <!-- -->
	                <div class="list-tail">
	                    <span class="classified"></span>
	                    <span></span>
	                    <span></span>
	                    <span></span>
	                    <span class="more" @click="more"></span>
	                </div>
	            </li>
	        </ul>
	        <!--  -->
            <div class="bottomLoad" v-if="contentJson.length > 0">
                <div class="loading" v-show='bottomLoading'>...</div>
                <div class="noData"  v-if='bottomNoData'></div>
            </div>
        </mt-loadmore>
    </div>
</template>

<script>
import { mapState, mapGetters, mapMutations, mapActions } from 'vuex'
import banner from "../../components/common/banner.vue";
// mint-ui 
import { Loadmore ,InfiniteScroll  } from 'mint-ui';
export default {
    data() {
        return {
        	receptionMSG: false,//
        	topStatus: '',          // 
            bottomLock: false,      // 
            bottomLoading: true,    // loading
            bottomNoData: false,    // nothing
            contentJson: [1,2],     // 
        };
    },
    components: {
        banner
    },
    mounted() {
    },
    computed: {
        ...mapState('commom', [
            'popUp',
            'popUpType'
        ]),
	},
	watch: {
		popUp() {
			//vuex
			if(this.receptionMSG){
				if(this.popUp){
					//
				}else{
                    this.receptionMSG = false;
					//
					if(this.popUpType == 1){
						console.log('');
					}else if(this.popUpType == 2){
						console.log('');
					}else if(this.popUpType == 3){
						console.log('');
					}
				}
			}else{
				return;
			}
		}
	},
    methods:{
        ...mapMutations('commom', [
	       'set_popUp'
		]),
    	//
    	more: function(){
            this.receptionMSG = true;
    		this.set_popUp(true);
    	},
    	
    	// mint-ui 
        handleTopChange(status) {
            this.topStatus = status;
        },

        // 
        loadTopAjax() {
            setTimeout(() => {
            this.$refs.loadmore.onTopLoaded();
            }, 1500);
        },
        // 
        loadBottomAjax() {
            this.bottomLock = true; // 
            setTimeout(() => {
                let arr1 = [3,3,4,5,6,7,8,99,9,0,2];
                this.contentJson.push(arr1);
                this.bottomLock = false; // 
                
            }, 1000);
            // alert(1);
            console.log(this.contentJson);
            this.bottomNoData = false;
            // this.contentJson = [];
        },
    	// 
        handleLocaltion(type) {
            if (type === 'get') {
                this.$nextTick(() => {
                	document.querySelector(".DisContainer").scrollTop = sessionStorage.getItem('disScrollTop');
            	})
            } else if (type === 'set') {
	            let scrollTop = document.querySelector(".DisContainer").scrollTop;
				sessionStorage.setItem('disScrollTop', scrollTop);
	        }
        },
        saveScroll() {
            this.handleLocaltion('set')
        },
    },
    activated() {
        this.handleLocaltion('get');
        window.addEventListener('beforeunload', this.saveScroll); // 
    },
    deactivated() {
        this.handleLocaltion('set');
    	window.addEventListener('beforeunload', this.saveScroll); // 
    }
};
</script>
<style scoped lang="scss">
	.DisContainer {
        height: 100%;
        overflow: auto;
        -webkit-overflow-scrolling: touch;
    }
    .live-shop-list{
        padding: 0 30px;
        box-sizing: border-box;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        li{
            border-bottom: 1px solid #c7c7c7;
            /*color: #000000;*/
            padding-top: 30px;
            .information-header {
                padding: 0 0 20px 0;
                font-size: 28px;
                font-weight: bold;
            }
            // 
            .user{
                width: 75%;
                .mg-top{
                    margin: 10px 0 0 0;
                }
            }
            .user-bg {
                float: left;
                width: 72px;
                height: 72px;
                background: url("../../assets/img/icon-User-Avatar.png") no-repeat left top;
                background-size: cover;
                margin: 0 20px 0 0;
            }
            span{
                font-weight: normal;
                color: #999999;
                font-size: 24px;
            }
            // 
            .money {
                font-size: 32px;
                color: #f05c28;
                margin: 16px 0 0 0;
               
            }
            .information-in{
                line-height: 1.5;
                font-size: 36px;
                overflow: hidden;
                text-overflow: ellipsis;
                display: -webkit-box;
                -webkit-line-clamp: 3;
                -webkit-box-orient: vertical;
            }
            // 
            .pic-box{
                display: -webkit-flex; /* Safari */
                display: flex;
                flex-wrap: wrap;
                justify-content: flex-start;
                margin: 24px 0 20px 0;
                span{
                    display: block;
                    overflow: hidden;
                }
                img{
                    max-height: 100%;
                    width: 100%;
                }
            }
            .pic-cont01 span{
                width:32.7%;
                max-height: 155px;
                margin: 0 0.6% 0.6% 0;
            }
            .pic-cont02 span{
                width:49%;
                margin: 0 0.8% 0.6% 0;                    
            }
            .pic-cont03 span{
                 width:60%;
            }
            //
            .list-tail{
                height: 40px;
                line-height: 40px;
                font-size: 24px;
                color: #999999;
                margin: 0 0 24px 0;
                span{
                    display: inline-block;
                    margin: 0 24px 0 0;
                }
                .classified{
                    border: 2px solid #f05c28;
                    height: 36px;
                    /*line-height: 36px;*/
                    border-radius: 10px;
                    -webkit-border-radius: 10px;
                    -moz-border-radius: 10px;
                    -o-border-radius: 10px;
                    border-radius: 10px;
                    color: #f05c28;
                    padding: 0 10px;
                }
                .more{
                    float: right;
                    height: 100%;
                    width: 42px;
                    background: url("../../assets/img/icon-more-life.png") no-repeat center center;
                    background-size: 100%;
                    margin: 0;
                }
            }
            &:last-child{
                border: none;
            }
        }
    }
    .bottomLoad {
        width: 100%;
        position: relative;
        div {
            width: 100%;
            height: 50px;
            line-height: 50px;
            text-align: center;
            font-size: 16px;
            color: #999;
        }
    }

</style>




